.MobileHome {
    padding: 1% 1% 1% 1%;
    width: 100%;
    height: 100%;
    position: absolute;
    background: #C3C3C3;
    display: grid;
    grid-template-areas: 
    "header"
    "Content"
    "SponsorLable"
    "SponsorsKit"
    "Registration"
    "Footer";
    grid-template-columns: 100%;
    grid-template-rows: 30% 20% 10% 20% 10% 10%;
}


.header {
    display: grid;
    grid-template-areas: 
    "About Logo";
    grid-template-columns: 25% 75%;
    grid-template-rows: 100%;
}
.AboutOlimpiad {
    grid-area: "About";
    background: #C3C3C3;
    box-shadow: inset -2px -2px 0px #262626, inset 2px 2px 0px #F0F0F0, inset -4px -4px 0px #7E7E7E;
}
.Logo {
    grid-area: "Logo";
    background-color:#008282;
}


.Content {
    padding-right: 1%;
    grid-area: "Content";
    background-color: #C3C3C3;
    display: grid;
    grid-template-areas: 
    "Ways Diaractions";
    grid-template-columns: 50% 50%;
}
.Ways {
    padding: 2%;
    grid-area: "Ways";
    display: grid;
    grid-template-areas: 
    "Way1 Way2"
    "Way3 Way4";
    grid-template-columns: 50% 50%;
    grid-template-rows: 50% 50%;
    grid-column-gap: 2%;
    grid-row-gap: 2%;
}
.Way1 {
    grid-area: "Way1";
    background: #C3C3C3;
    box-shadow: inset -2px -2px 0px #262626, inset 2px 2px 0px #F0F0F0, inset -4px -4px 0px #7E7E7E;
}
.Way2 {
    grid-area: "Way2";
    background: #C3C3C3;
    box-shadow: inset -2px -2px 0px #262626, inset 2px 2px 0px #F0F0F0, inset -4px -4px 0px #7E7E7E;
}
.Way3 {
    grid-area: "Way3";
    background: #C3C3C3;
    box-shadow: inset -2px -2px 0px #262626, inset 2px 2px 0px #F0F0F0, inset -4px -4px 0px #7E7E7E;
}
.Way4 {
    grid-area: "Way4";
    background: #C3C3C3;
    box-shadow: inset -2px -2px 0px #262626, inset 2px 2px 0px #F0F0F0, inset -4px -4px 0px #7E7E7E;
}
.Diaractions {
    background-color: cadetblue;
    grid-area: "Diaractions";
}

.SponsorLable {
    grid-area: "SponsorLable";
    background: #C3C3C3;
}

.SponsorsKit {
    padding-right: 2%;
    grid-area: "SponsorsKit";
    display: grid;
    grid-template-areas: 
    "Sponsor1 Sponsor2"
    "Sponsor3 Sponsor4";
    grid-template-columns: 50% 50%;
    grid-template-rows: 50% 50%;
    grid-column-gap: 1%;
    grid-row-gap: 1%;
    padding-bottom: 1%;
}
.Sponsor1 {
    grid-area: "Sponsor1";
    background: #C3C3C3;
    box-shadow: inset -2px -2px 0px #262626, inset 2px 2px 0px #F0F0F0, inset -4px -4px 0px #7E7E7E;
}
.Sponsor2 {
    grid-area: "Sponsor2";
    background: #C3C3C3;
    box-shadow: inset -2px -2px 0px #262626, inset 2px 2px 0px #F0F0F0, inset -4px -4px 0px #7E7E7E;
}
.Sponsor3 {
    grid-area: "Sponsor3";
    background: #C3C3C3;
    box-shadow: inset -2px -2px 0px #262626, inset 2px 2px 0px #F0F0F0, inset -4px -4px 0px #7E7E7E;
}
.Sponsor4 {
    grid-area: "Sponsor4";
    background: #C3C3C3;
    box-shadow: inset -2px -2px 0px #262626, inset 2px 2px 0px #F0F0F0, inset -4px -4px 0px #7E7E7E;
}

.Registration {
    grid-area: "/Registration";
    text-decoration: none;
    color:#262626;
    background: #C3C3C3;
    box-shadow: inset -2px -2px 0px #262626, inset 2px 2px 0px #F0F0F0, inset -4px -4px 0px #7E7E7E;
}

.Footer {
    grid-area: "Footer";
    background: #C3C3C3;
}